.events-container {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	margin-top: 15px;
	box-shadow: -1px 41px 46px 1px rgba(0, 0, 0, 0.26);
	-webkit-box-shadow: -1px 41px 46px 1px rgba(0, 0, 0, 0.26);
	-moz-box-shadow: -1px 41px 46px 1px rgba(0, 0, 0, 0.26);
	border-radius: 20px;
	user-select: none;
	padding: 20px;
	background-color: var(--inputBackColor);
	min-height: 65px;
}
